@use "base/mixins" as m;
@use "base/variables" as v;

@include m.tablet {
  .about-icons {
    @include m.grid(3, 2rem);
  }
}
.icon {
  img {
    height: 10rem;
  }
  h3 {
    text-transform: uppercase;
    text-align: center;
    font-size: 2.5rem;
  }
  p {
    text-align: center;
  }
}
