  display: none;
}

.yellow-btn {
  background-color: #e08709;
  color: #ffffff;
  font-weight: 500;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.yellow-btn:hover {
  background-color: #c77808;
  cursor: pointer;
}

.yellow-btn-inb {
  background-color: #e08709;
  color: #ffffff;
  font-weight: 500;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.yellow-btn-inb:hover {
  background-color: #c77808;
  cursor: pointer;
}

.green-btn {
  background-color: #71b100;
  color: #ffffff;
  font-weight: 500;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.green-btn:hover {
  background-color: #619800;
  cursor: pointer;
}

.form p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 2rem 0;
}
.form legend {
  color: #333333;
  font-size: 2rem;
}
.form label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
  margin: 1rem 0 1rem 0;
}
.form input:not([type=submit]),
.form textarea,
.form select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #ffffff;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
}
.form textarea {
  height: 20rem;
}
.form .contact-radio {
  max-width: 25rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form input [type=radio] {
  margin: 0;
  width: auto;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #ffffff;
}
.dark-mode a,
.dark-mode h1,
.dark-mode h2,
.dark-mode h3,
.dark-mode h4,
.dark-mode blockquote {
  color: #e1e1e1;
}
.dark-mode .price {
  color: #71b100;
}
.dark-mode .dark-icon {
  filter: invert(100%);
}
.dark-mode .card {
  background-color: #333333;
  border: 1px solid #4d4d4d;
}
.dark-mode .form label,
.dark-mode .form legend,
.dark-mode .form p {
  color: #e1e1e1;
}
.dark-mode .form input:not([type=submit]),
.dark-mode .form textarea,
.dark-mode .form select {
  background-color: #333333;
  border: 0px;
  color: #ffffff;
}

.no-webp .home {
  background-image: url(../img/header.jpg);
}

.webp .home {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 3rem 0 3rem 0;
  background-position: center center;
  background-size: cover;
}
.header.home {
  min-height: 48rem;
}
@media (min-width: 768px) {
  .header.home {
    min-height: 60rem;
  }
}
@media (min-width: 768px) {
  .header.home .header-content {
    min-height: 60rem;
  }
}

.header-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header-content h1 {
  text-align: left;
  color: #ffffff;
  font-weight: 500;
  max-width: 43rem;
}

.footer {
  padding: 2.5rem 0;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .footer-container {
  padding-top: 2.5rem;
}
.footer .navbar {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #ffffff;
  text-align: center;
}

.right {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .right {
    align-items: flex-end;
  }
}
.right .toggle {
  background: linear-gradient(145deg, #e1e1e1, #ffffff);
  box-shadow: 1px 1px 2px #e6e6e6, -1px -1px 2px #ffffff;
  transition: 0.2s;
  cursor: pointer;
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 8rem;
  height: 3rem;
  border-radius: 5rem;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .right .toggle {
    margin-bottom: 1rem;
  }
}
.right .toggle .bx-sun {
  color: #fafafa;
}
.right .toggle .bxs-moon {
  color: #151d2a;
}
.right .toggle .ball {
  background: #151d2a;
  transition: 0.3s ease-out;
}
.right .toggle i {
  width: 50%;
  line-height: 3rem;
  text-align: center;
  font-size: 1.6rem;
  z-index: 1;
}
.right .toggle .ball {
  position: absolute;
  width: 2.5rem;
  height: 2.5rem;
  margin: 0.2rem 0.2rem 0.2rem 0.8rem;
  border-radius: 50%;
}

.dark-mode .toggle {
  background: #151d2a;
  box-shadow: 1px 1px 2px #0e131b, -1px -1px 2px #1c2739;
  transition: 0.2s;
}
.dark-mode .toggle .bxs-sun {
  color: #fafafa;
}
.dark-mode .toggle .bx-moon {
  color: #151d2a;
}
.dark-mode .toggle .ball {
  background: #fafafa;
  transform: translateX(4rem);
  transition: 0.3s ease-out;
}

.bar {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navbar {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.3s linear;
  height: 0;
}
@media (min-width: 768px) {
  .navbar {
    display: flex;
    align-items: center;
    visibility: visible;
    opacity: 1;
  }
}
.navbar a {
  color: #ffffff;
  text-decoration: none;
  display: block;
  text-align: center;
  font-weight: 700;
  padding: 1rem 0 0 0;
}
@media (min-width: 768px) {
  .navbar a {
    margin-right: 2rem;
  }
  .navbar a:last-of-type {
    margin-right: 0;
  }
}
.navbar a:hover {
  color: #71b100;
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 3rem;
}

.show-menu {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .about-icons {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icon img {
  height: 10rem;
}
.icon h3 {
  text-transform: uppercase;
  text-align: center;
  font-size: 2.5rem;
}
.icon p {
  text-align: center;
}

@media (min-width: 768px) {
  .card-container {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.card {
  background-color: #eeeeee;
  border: 1px solid #d4d4d4;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.card img {
  border-radius: 1rem 1rem 0 0;
}
.card .card-content {
  padding: 1rem;
}
.card h3,
.card p {
  margin: 0 0 1rem 0;
}

.price {
  color: #71b100;
  font-weight: 700;
  font-size: 2.5rem;
  margin: 1rem 0 1rem 0;
}

.feature-icons {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.feature-icons li {
  flex: 1;
  display: flex;
}
.feature-icons li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.feature-icons li p {
  font-weight: 900;
}

.no-webp .contact-image {
  background-image: url("../img/encuentra.jpg");
}

.webp .contact-image {
  background-image: url("../img/encuentra.webp");
}

.contact-image {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contact-image h2 {
  font-weight: 700;
  color: #ffffff;
}
.contact-image p {
  color: #ffffff;
}

@media (min-width: 768px) {
  .lower-section {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.blog-entry {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .blog-entry {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 1rem;
  }
}
.blog-entry:last-of-type {
  margin-bottom: 0;
}

.blog-data span {
  color: #e08709;
}

.entry-text a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .entry-text a {
    margin-top: 0;
  }
}
.entry-text h4 {
  margin: 0;
  line-height: 1.4rem;
}
.entry-text h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71b100;
  margin-top: 1rem;
}

.testimonial {
  background-color: #71b100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #ffffff;
  font-weight: 300;
  border-radius: 3%;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  display: block;
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  position: absolute;
  left: -2rem;
  top: 1rem;
}
.testimonial p {
  color: #ffffff;
  text-align: right;
}

@media (min-width: 768px) {
  .about-content {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .about-content .about-text p {
    text-align: left;
  }
}

.about-text blockquote {
  font-weight: 700;
  font-size: 2.2rem;
  margin: 0;
  padding: 1rem 0 3rem 0;
}
.about-text p {
  text-align: center;
}

.alert {
  padding: 0.5rem;
  margin: 1rem 0;
  color: #ffffff;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
}
.alert.error {
  background-color: #ff0f0f;
}